                <div class="source" slot="source"><template>
  <div class="inline">
    <bee-button @click="message1()">默认</bee-button>
    <bee-button theme="success" @click="message1('success')">success</bee-button>
    <bee-button theme="error" @click="message1('error')">error</bee-button>
  </div>
</template>

</div>
                
                <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'inline'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"message1()"</span>&gt;</span>默认<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">theme</span>=<span class="hljs-string">'success'</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"message1('success')"</span>&gt;</span>success<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">theme</span>=<span class="hljs-string">'error'</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"message1('error')"</span>&gt;</span>error<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    message1 (type) {
      <span class="hljs-keyword">let</span> options = {
        <span class="hljs-attr">message</span>: <span class="hljs-string">'这是一段提示文字。'</span>,
        <span class="hljs-attr">duration</span>: <span class="hljs-number">3</span>
      }

      type ? options.type = type : <span class="hljs-literal">null</span>

      <span class="hljs-keyword">this</span>.$_createMessage(options).show()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>手动关闭</h3>
<demo-block>
                <div class="source" slot="source"><template>
  <div class="inline">
    <bee-button @click="message2()">显示</bee-button>
    <bee-button @click="remove2()">移除</bee-button>
  </div>
</template>

</div>
                
                <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'inline'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"message2()"</span>&gt;</span>显示<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"remove2()"</span>&gt;</span>移除<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">messages</span>: []
    }
  },
  <span class="hljs-attr">methods</span>: {
    message2 () {
      <span class="hljs-keyword">let</span> item = <span class="hljs-keyword">this</span>.$_createMessage({
        <span class="hljs-attr">message</span>: <span class="hljs-string">'不会自动移除的提示。'</span>,
        <span class="hljs-attr">duration</span>: <span class="hljs-number">0</span>
      })
      
      item.show()

      <span class="hljs-keyword">this</span>.messages.push(item)
    },

    remove2 () {
      <span class="hljs-keyword">let</span> item = <span class="hljs-keyword">this</span>.messages.pop()
      item.close()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>使用HTML</h3>
