                <div class="source" slot="source"><template>
  <div class="inline">
    <bee-button @click="message3()">使用HTML</bee-button>
  </div>
</template>

</div>
                
                <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'inline'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"message3()"</span>&gt;</span>使用HTML<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
