<h3>其他用法</h3>
<demo-block>
                <div class="source" slot="source"><template>
  <div class="inline">
    <bee-button @click="message4('left')">left</bee-button>
    <bee-button @click="message4('center')">center</bee-button>
  </div>
</template>

</div>
                
                <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'inline'</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"message4('left')"</span>&gt;</span>left<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">bee-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"message4('center')"</span>&gt;</span>center<span class="hljs-tag">&lt;/<span class="hljs-name">bee-button</span>&gt;</span>
