        <span class="hljs-attr">message</span>: <span class="hljs-string">'居中对其'</span>,
        <span class="hljs-attr">align</span>: align
      }

      <span class="hljs-keyword">this</span>.$_createMessage(options).show()
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
