<h3>options</h3>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td>string</td>
<td>warn,success,error</td>
<td>warn</td>
</tr>
<tr>
<td>duration</td>
<td>显示的时长</td>
<td>number</td>
<td>—</td>
<td>3</td>
</tr>
<tr>
<td>message</td>
<td>提示的文字信息</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>html</td>
<td>提示的HTML代码</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>align</td>
<td>提示文字的对其方式</td>
<td>left,center</td>
<td>—</td>
<td>left</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.inline .btn--wp {
  margin: 0 10px 10px 0;
}
</style>
<script>
export default {
  data () {
    return {
      messages: []
    }
  },
  methods: {
    message1 (type) {
      let options = {
        message: '这是一段提示文字。',
        duration: 3
      }

      type ? options.type = type : null

      this.$_createMessage(options).show()
    },
    message2 () {
      let item = this.$_createMessage({
        message: '不会自动移除的提示。',
        duration: 0
      })
      
      item.show()
      this.messages.push(item)
    },

    remove2 () {
      let item = this.messages.pop()
      item && item.close()
    },
    message3 () {
      this.$_createMessage({
        html: `<span style='color: red;'>这是一段 <span style='font-size: 20px;'> HTML </span>提示。</span>`
      }).show()
    },
    message4 (align) {
      let options = {
        message: '居中对其',
        align: align
      }

      this.$_createMessage(options).show()
    }
  }
}
</script>